import React from 'react';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import "src/components/carousel/carousel.css";

const importAll = (r)=> {
    return r.keys().map(r);
}
    
const images = importAll(require.context('src/assets/galery/', false, /\.(png|jpe?g|svg)$/));
// eslint-disable-next-line import/no-anonymous-default-export
export default () => {
    const theme = useTheme();
    const matches =  useMediaQuery(theme.breakpoints.up('sm'));
    const matchesMd =  useMediaQuery(theme.breakpoints.up('md'));
    return(
    
        <Carousel 
            showThumbs={false} 
            // showThumbs={matches ? true : false} 
            thumbWidth={300}
            showIndicators={false} 
            showStatus
            // showStatus={matches ? false : true}
            showArrows
            // showArrows={matches ? false : true}
            autoPlay
            stopOnHover
            useKeyboardArrows
            infiniteLoop
            centerMode={matches ? true : false}
            centerSlidePercentage={matchesMd ? 20 : 40}
            statusFormatter={(item, total) =>(`${item} de ${total}`)}
        >
            {images.map((itemImg, index)=>(
                <div key={index}>
                <img alt="" src={itemImg.default} />
            </div>
                
                ))
            }
            
        </Carousel>
    )
}
